<template>
    <main>
        <page>
            <div class="content">

                <window class="winodw">
                    <div>

                        <div>
                            <div class="header">
                                <h3>По системе</h3>
                            </div>

                            <div class="items">
                                <div class="input-button">
                                    <label for="system-name"><span>Название системы</span></label>
                                    <div>
                                        <input v-model="system.name" id="system-name" type="text" placeholder="Выгодный контракт"/>
                                        <button @click="changeSystemData" class="btn btn-green btn-small"><i class="icon icon-ok"></i></button>
                                    </div>
                                </div>

                                <div class="input-button mt-5">
                                    <label for="system-phone"><span>Номер телефона</span></label>
                                    <div>
                                        <input v-model="system.phone" id="system-phone" type="text" placeholder="+7(999) 99-999-99"/>
                                        <button @click="changeSystemData" class="btn btn-green btn-small"><i class="icon icon-ok"></i></button>
                                    </div>
                                </div>

                                <div class="input-button mt-5">
                                    <label for="system-email"><span>Email</span></label>
                                    <div>
                                        <input v-model="system.email" id="system-email" type="text" placeholder="example@example.com"/>
                                        <button @click="changeSystemData" class="btn btn-green btn-small"><i class="icon icon-ok"></i></button>
                                    </div>
                                </div>

                            </div>

                            <div class="items">
                                <handbook-item v-if="isLoaded" :list="units" name="purchase/units">Единицы измерения <small class="color-red">(ЛУЧШЕ НИЧЕГО НЕ УДАЛЯТЬ В
                                    ПРОДАКШЕНЕ)</small>
                                </handbook-item>
                            </div>

                        </div>

                        <div>
                            <div class="header">
                                <h3>По пользователям</h3>
                            </div>
                            <div class="items">
                                <handbook-item v-if="isLoaded" :list="types" name="user/types">Типы пользователей <small class="color-red">(ЛУЧШЕ НИЧЕГО НЕ УДАЛЯТЬ В
                                    ПРОДАКШЕНЕ)</small>
                                </handbook-item>
                            </div>
                        </div>

                        <div>
                            <div class="header">
                                <h3>По закупкам</h3>

                            </div>

                            <div class="items">
                                <handbook-item v-if="isLoaded" :list="methods" name="purchase/methods">Методы закупки <small class="color-red">(ЛУЧШЕ НИЧЕГО НЕ УДАЛЯТЬ В
                                    ПРОДАКШЕНЕ)</small>
                                </handbook-item>
                            </div>

                            <div class="items">
                                <handbook-item v-if="isLoaded" :list="statuses" name="purchase/statuses">Статусы <small class="color-red">(ЛУЧШЕ НИЧЕГО НЕ УДАЛЯТЬ В
                                    ПРОДАКШЕНЕ)</small>
                                </handbook-item>
                            </div>
                        </div>

                    </div>
                </window>

            </div>
        </page>
    </main>
</template>

<script>
    import Window from "../../components/anothers/Window";
    import Page from "../../components/anothers/Page";
    import HandbookItem from "../../components/handbooks/handbook-item";
    import axios from "axios";

    export default {
        name: 'AdminHandbooks',
        components: {HandbookItem, Page, Window},
        data: function () {
            return {
                isLoaded: false,
                methods: [],
                types: [],
                statuses: [],
                units: [],
                system: this.$store.getters.GET_SYSTEM
            }
        },
        methods: {
            changeSystemData(){
                axios.post('system', null, {
                    params: {
                        name: this.system.name,
                        phone: this.system.phone,
                        email: this.system.email
                    }
                }).then(system => {
                    this.$store.dispatch('UPDATE_SYSTEM', system.config.params);
                    this.$store.commit('ADD_HANDLER', {
                        text: "Успешно изменено",
                        classes: 'success'
                    })
                }).catch(err => this.$store.commit('ADD_HANDLER', {
                    text: "Ошибка запроса... " + err,
                    classes: 'error'
                }))
            }
        },
        created: function () {
            axios.get('handbook/purchase/methods').then(res => {
                this.methods = res.data;

                axios.get('handbook/user/types').then(res => {
                    this.types = res.data;

                    axios.get('handbook/purchase/statuses').then(res => {
                        this.statuses = res.data.filter(e => e.id !== 1 && e.id !== 2 && e.id !== 3);

                        axios.get('handbook/purchase/units').then(res => {
                            this.units = res.data;
                            this.isLoaded = true;

                        }).catch(err => this.$store.commit('ADD_HANDLER', {
                            text: "Ошибка получения данных... " + err,
                            classes: 'error'
                        }))
                    }).catch(err => this.$store.commit('ADD_HANDLER', {
                        text: "Ошибка получения данных... " + err,
                        classes: 'error'
                    }))
                }).catch(err => this.$store.commit('ADD_HANDLER', {
                    text: "Ошибка получения данных... " + err,
                    classes: 'error'
                }))
            }).catch(err => this.$store.commit('ADD_HANDLER', {
                text: "Ошибка получения данных... " + err,
                classes: 'error'
            }))
        }
    }
</script>

<style lang="scss" scoped>
    .window {
        margin-top : 40px;

        & > div {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            & > div {
                padding : 20px;
                box-sizing: border-box;

                & > .header {
                    padding   : 0 0 20px 0;
                    font-size : 1em;
                }

                & > .items {
                    margin : 20px 0;
                }
            }
        }
    }
</style>