<template>
    <section class="page">
        <header>
            <slot name="header">
                <h2>{{$route.name}}</h2>
            </slot>
        </header>

        <main>
            <slot></slot>
        </main>
    </section>
</template>

<style lang="scss">
    .page {

        & > header {
            height: 90px;
            display: flex;
            align-items: center;

            h2 {
                font-family    : "Proxima Nova Rg", "Arial", serif;
                font-weight    : bold;
                color          : #33A54B;
                padding-left   : 80px;
                position       : relative;
                font-size      : 1.6em;
                text-transform : uppercase;
                display        : table;

                @media(max-width: 500px){
                    padding-left: 30px;
                    font-size: 1.3em;
                }

                &:before {
                    content    : '';
                    position   : absolute;
                    left       : 0;
                    bottom     : -5px;
                    background : #33A54B;
                    height     : 1.5px;
                    width      : 102%;
                }
            }
        }

        & > main {
            padding    : 0 30px 30px 30px;
            box-sizing : border-box;
            max-width  : 100%;
        }
    }
</style>

<script>
    export default {
        name: "Page"
    }
</script>